import React, { Component } from 'react';

import { CircularProgress, Box, Paper, Typography, Button } from '@material-ui/core';

class StatsBox extends Component {
    constructor(props) {
        super(props);

    }

    render() {
        return(
            <Box>
                <Typography variant="body1">Time: {this.props.time}</Typography>
                <Typography variant="body1">Correct Words: {this.props.words}</Typography>
                <Typography variant="body1">WPM: {Math.round(60 * this.props.words / this.props.time)}</Typography>
            </Box>
        )
    }
}

export default StatsBox;