import React, { Component } from 'react';

import { CircularProgress, Box, Paper, Typography, Button } from '@material-ui/core';

class ActionBox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showStart: false,
        }
    }

    render() {
        return(
            <Box p={2}>
            { this.props.loading && <CircularProgress size={90} /> }
            {this.props.showStart ? <Button variant="contained" color="primary" onClick={this.props.startClick}>Start Game</Button> : 
            <Typography align="center" variant="body1">Please wait for the game leader.</Typography>}
            </Box>
        )
    }
}

export default ActionBox;
